@font-face {
    font-family: 'gil';
    src:  url('../../font/gil.woff') format('woff2'),
  }


  body{
      font-family: 'gil';
  }

.header{
    background: #F6F6F7;
    height: 80px;
    display: flex;
    align-items: center;
}

a{
    color: #fff;
}

.logo{
    text-align: left;  
    padding-left: 20px;
    width: 150px;
}


.dp{
    display: flex;
    margin: 30px 0 !important;
    justify-content: center;
    

}

.para_landing{
    padding: 0 10%;
    text-align: center;
}

.dp_img{
    width: 300px;
    border-radius: 50%;
    margin-top: 20px;
}

.name{
    text-align: center;
    margin-bottom: 10px;
}

.my_exper, .myclient{
    margin-top: 60px;
}

.diagram{
    width: 400px;
    margin-top: 40px;
}

.experience{
    width: 60%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    padding: 5px 10%;
    height: 80px;
    background: #F6F6F7;
    margin-top: 20px;
    align-items: center;
    border-radius: 10px;

}

.client{
    width: 60%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    padding: 5px 10%;
    height: 80px;
    background: #F6F6F7;
    margin-top: 20px;
    align-items: center;
    border-radius: 10px;
    overflow: hidden;
    transition: all 0.3s ease-in-out;
}



.exper_img{
    height: 60px;
    width:60px;
}

.client_img{
    width: 200px;
}


.secodary{
    width: 200px;
    
}




.footer{
    background: #db1947;
    padding: 50px 100px;
    margin-top: 80px;


    h3{
        color: #fff;
        font-size: 30px;
        text-align: right;
        padding: 0 !important;
        margin: 0 !important;

    }

    p{
        color: #fff;
        text-align: right;
        padding: 0 !important;
        margin: 0 !important;
    }
}


.internal_div{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 80%;



p{
    justify-content: center;
    text-align: left;
    padding-left: 30px;
}
}

.copy_footer{
    text-align: left !important;
}

.icons_footer{
    color: #fff;
    font-size: 26px;
    text-align: right;
    margin-top: 10px;
    
}

.client_para, .more_work{
    display: none;
    text-align: center;
    font-size: 20px;
}

.client_last:hover{
    background: #FFF5E9 !important;

    
a{
    color:#e37000 !important;
}
}

.client:hover{
    background: #ffe4f3;

    .client_img, .secodary{
        display: none;
    }

    .client_para{
        display: block;
        width: 100%;
        color: #ed2863 !important;
        font-size: 24px;
        font-weight: 600;
    }

    .more_work{
        display: block;
        width: 100%;
        color: #e37000 !important;
        font-size: 24px;
        font-weight: 600;
        
    }

    a{
        color: #ed2863;
    }

}


.icon1{
    padding: 0 5px
}


@media(max-width:500px){
    .dp_img{
        width: 250px;
    }
    
    .diagram{
        width: 250px;
    }

    .internal_div{
        align-items: flex-start !important;
    }


.footer{
    
    background: #db1947;
    padding: 10px 10px;
    margin-top: 80px;
    
    h3{
        color: #fff;
        font-size: 20px;
        text-align: right;
        padding: 0 !important;
        margin: 0 !important;

    }

    p{
        font-size: 16px;
        color: #fff;
        text-align: right;
        padding: 0 !important;
        margin: 0 !important;
    }
}

.client:hover{


    .client_para{
        display: block;
        width: 100%;
        color: #ed2863 !important;
        font-size: 20px;
        font-weight: 600;
    }

    .more_work{
        display: block;
        width: 100%;
        font-size: 20px;
        font-weight: 600;
    }

       
}


.experience{
    padding: 10px 15px;
    width: 80%;

    .internal_div p{
        text-align: left;

    }
}
}

@media (max-width:600px){
    .internal_div{
        flex-direction: column;
        padding: 10px 0;

        p{
            padding: 0;
            text-align: center;
        }
    }

    .experience{
        height: auto;
    }
}


*{
    margin: 0;
    padding: 0;
    text-decoration: none;
}



@media (max-width:400px) {
    
    .client_para{
        display: block;
        width: 100%;
        font-size: 18px !important;
        font-weight: 600;
    }
}